@import '../../styles/variables.scss';

.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	&__logo {
		@include fontMontserrat(24, 30);
	}
	&__nav {
	}
	&__tools {
		&_profile {
			&::before {
				content: url('../../assets/icons/user.svg');
				width: 12px;
				height: 15px;
				display: block;
			}
		}
	}
}

.nav {
	&__list {
		display: flex;
		gap: 40px;
		@include fontMontserrat(10, 12, 700, $colorTextGrey);
		text-transform: uppercase;
	}
}